<template>
    <div>
        <collapse-button v-bind:collapsed="collapsed" @collapse="collapse"/>
        <h2>Tags:</h2>
        <div v-if="!collapsed">
            <ol class="tag-list">
                <li v-for='tag in tags' v-bind:key='tag' v-bind:tag='tag'><span class="tag">{{ tag }}</span>:
                    <ol class="artist-list">
                        <li v-for='tagging in taggings[tag]' v-bind:key='tagging.artist'>{{ tagging.artist }}</li>
                    </ol>
                </li>
            </ol>
        </div>
    </div>
</template>

<script>
    import CollapseButton from "./CollapseButton.vue"

    export default {
        components: {
            CollapseButton
        },
        props: ['tags','taggings'],
        data: function(){
            return {
                collapsed:true,
            }
        },
        methods: {
            collapse: function(){
                this.collapsed = !this.collapsed
            }
        }
    }
</script>

<style scoped>
    h2 { display:inline; }

    .tag-list {
        margin: 0.5vw 0 0 0;
        padding: 1vw 0 1vw 2vw;
        border-width:1px;
        border-style:solid;
    }

    .tag { font-weight:bold; }

    .artist-list { display:inline; }
    .artist-list li { display:inline; }

    .artist-list li:before { content:", "; }
    .artist-list li:first-child:before { content:""; }
</style>