<template>
    <div>
        <collapse-button v-bind:collapsed="collapsed" @collapse="collapse"/>
        <h2>Artists:</h2>
        <div v-if="!collapsed">
            <ol id="artist-list">
                <li v-for='artist in artists' v-bind:key='artist' v-bind:artist='artist'>
                    <span class="artist">{{ artist }}</span> ({{ listens[artist] }} <span v-if="listens[artist]>1">listens</span> <span v-else>listen</span>)
                </li>
            </ol>
        </div>
    </div>
</template>

<script>
    import CollapseButton from "./CollapseButton.vue"

    export default {
        components: {
            CollapseButton
        },
        props: ['artists','listens'],
        data: function(){
            return {
                collapsed:true,
            }
        },
        methods: {
            collapse: function(){
                this.collapsed = !this.collapsed
            }
        }
    }
</script>

<style scoped>
    h2 { display:inline; }

    #artist-list {
        margin: 0.5vw 0 0 0;
        padding: 1vw 0 1vw 2vw;
        border-width:1px;
        border-style:solid;
    }

    .artist { font-weight:bold; }
</style>